  transition: transform .3s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateY(-100%);
}

.page__menu:target {
  pointer-events: all;
  transform: translateY(0);
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page ::-webkit-scrollbar {
  width: 15px;
}

.page ::-webkit-scrollbar-track {
  background: #d0d0d0;
}

