.icon--menu, .icon--close, .icon--facebook, .icon--twitter, .icon--instagram, .icon--arrow-up {
  transition: background-color .3s;
}

.icon--menu:hover, .icon--close:hover, .icon--facebook:hover, .icon--twitter:hover, .icon--instagram:hover, .icon--arrow-up:hover {
