}

.icon--menu {
  mask-image: url("icon-burger-menu.3646686a.svg");
}

.icon--close {
  mask-image: url("icon-close.417bedc0.svg");
}

.icon--facebook {
  mask-image: url("icon-facebook.f6ab68ba.svg");
}

.icon--twitter {
  mask-image: url("icon-twitter.59f7364e.svg");
}

.icon--instagram {
  mask-image: url("icon-instagram.d115a830.svg");
}

.icon--arrow-up {
  mask-image: url("icon-arrow-up.74e336a3.svg");
}

.icon--arrow-left--stylish {
  mask-image: url("icon-arrow-left--btn.634f0220.svg");
}

.icon--arrow-right--stylish {
  transform: rotate(180deg);
  mask-image: url("icon-arrow-left--btn.634f0220.svg");
}

