.btn.btn--secondary--accent.btn--borderless .btn__text {
  position: relative;
}

.btn.btn--secondary--accent.btn--borderless .btn__text:after {
  content: "";
  background-color: #0db2b3;
  width: 42%;
  height: 1px;
  transition: width .3s ease-in-out;
  display: block;
  position: absolute;
  bottom: -7px;
}

.btn.btn--secondary--accent.btn--borderless .btn__text:hover:after {
  width: 100%;
}

